*{
    padding: 0;
    margin: 0;
}

@font-face
{
    font-family: "Jost";
    src: url("DressedlessProRegular.ttf") format("truetype");
    font-style: normal;
}

div{
    font-family: Jost;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
p{
    font-family: Jost;
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    line-height: 3.5vh;
}

img{
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}

body{
    background-image: url(cosmos.jpg);
    background-size: 100%;
}

.Container{
    margin: 0 auto;
    padding: 15px;
    width:  85%;
    height: 95vh;
    display: flex;
    justify-content: space-between;
}

.menu1{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu2{
    width: 50%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px;
    box-sizing: border-box;
}

.itog{
    width: 65%;
    height: 80%;
    background-image: url("blackhole.png");
    background-size: 100%;
    background-position-y: center;
    background-repeat: no-repeat;
    display: block;
    justify-content: center;
    align-items: center;
    /* animation: animspn 10s linear infinite; */
}
.itog:hover{
    animation: animspn 40s linear infinite 2s;
}

@keyframes animspn{
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg) scale(0.1);
    }
}
.itog_text{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.itog_text p{
    color: white;
    font-size: 2vw;
    line-height: 4vw;
    text-align: center;
    align-items: center;
    z-index: 2;
}

.itog_text p:active{
    transition: all 0.02s ease 0s;
    transform: scale(1.01);
    text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.2);
}

.itog_rgb{
    transition: background-color 0.6s;
    width: 45%;
    position:relative;
    top: 44.3%;
    left: 49.8%;
    border-radius: 50%;
    box-shadow: 0px 0px 30px 0.2px rgba(255, 255, 255, 0.5);
    transform: translate(-50%,-50%);
    background-color: aqua;
    display: none;
}

.itog_rgb:after{
    content: "";
    display: block;
    padding-bottom: 100%;
}

.item{
    height: 10%;
    margin-top: 2%;
    width: 90%;
    display:flex;
    justify-content: space-between;
}

.point{
    width: 45%;
    background-color: none;
    display: flex;
    align-items: center;
}

.point p{
    margin-left: 10px;
}

.button_random{
    width: 90%;
    height: 10%;
    margin-top: 30%;
    min-width: 240px;
    min-height: 90px;
    background: #b2e3f0;
    box-shadow: 0px 0px 20px 3px rgba(255, 255, 255, 0.3);
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5vh;
    font-family: Jost;
    cursor: pointer;
}

.button_random:hover{
    transition: all 0.2s ease 0s;
    transform: scale(1.01);
    box-shadow: 0px 0px 26px 4px rgba(255, 255, 255, 0.3);
    background: #c5f2fd;
}

.button_random:active{
    transition: all 0.07s ease 0s;
    transform: scale(1);
    background: #85b3be;
}

.point input[type="checkbox"]{
    display: none;
}

.point p{
    color: white;
    float: left;
    height: 100%;
    width: 60%;
    margin-left: 4%;
    text-align: left;
    font-size: 1.8vw;
    line-height: 2.2vw;
    display: flex;
    align-items: center;
}

label{
    width: 50px;  
}
label:before {
    content: " ";
    display: block;
    width: 50px;
    height: 50px;
    border: 6px solid white;
    border-radius: 50%;
    box-sizing: border-box;
}

input:checked + label:before {
    background-image: url("tochka.png");
    background-size: 100%;
    box-shadow: 0px 0px 30px 0.4px rgba(255, 255, 255, 0.5);
}

input:disabled + label:before {
background: #eee;
color: #aaa;
}

.itog_bone{
    transition: width 0.7s, opacity 0.5s, transform 1.2s;
    width: 2vw;
    position:relative;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: 0%;
}

.spin{
    display: block;
    transition: width 0.8s, opacity 1.2s, transform 0.7s;
    transform: translate(-50%,-50%) rotate(360deg);
    width: 12vw;
    opacity: 100%;
}

.layer{
    height: 150%;
    width: 150%;
    transform: translate(-50%,-50%);
    position: absolute;
    background-size: 100%;
}

.b1{
    background: url("Back1.png") repeat;
    background-size: 25%;
    z-index: -3;
    height: 98vh;
    width: 98vw;
    left: 1%;
    top: 0%;
}

.b2{
    background: url("Back2.png") repeat;
    background-size: 50%;
    z-index: -2;
    height: 98vh;
    width: 98vw;
    left: -34%;
    top: 0%;
}

@media (min-width: 1300px){
    .item{
        height: 12%;
    }
    .button_random{
        margin-top: 10%;
    }
    .itog_rgb{
        top:-50%;
    }
    .itog_bone{
        top:-50%;
    }
    .spin{
        width: 50%;
    }
}

@media (min-width: 600px) and (max-width: 1300px){
    .menu1{
        height: 80%;
    }
    .itog{
        width: 80%;
    }
    .itog_rgb{
        top:-50%;
    }
    .itog_bone{
        top:-50%;
    }
    .spin{
        width: 50%;
    }
}

@media (min-width: 0px) and (max-width: 600px){
    .menu1{
        height: 35%;
        width: 85%;
        margin: 0 auto;
    }
    .menu2{
        margin: 0 0 12%;
        width: 90%;
        height: 65%;
        padding: 0;
    }
    .item{
        width: 100%;
        margin-left: 8%;
    }
    .Container{
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .point p{
        font-size: 4vw;
        line-height: 4.3vw;
    }
    .spin{
        width: 21vw;
    }
    .itog_rgb{
        top: -50%;
    }
    .button_random{
        margin-top: 10%;
    }
    .spin{
        top:-50%;
    }
    .itog_bone{
        top:-50%;
    }
}

